<template>
  <v-container>
    <v-row class="text-center">
      <v-col cols="12"></v-col>

      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          {{result}} מתפללים
          <br>ב -
          <br>
          {{num_groups}} קפסולות
        </h1>

        <p class="subheading font-weight-regular">
          השימוש במחשבון על אחריות המשתמש בלבד
          <br>להוראות משרד הבריאות
          <a href="https://www.gov.il/he/departments/news/13092020_04" target="_blank">הקליקו כאן</a>
        </p>
      </v-col>

      <v-col class="mb-5" cols="12">
        <v-row justify="center">
          <v-col class="d-flex" cols="12" sm="6">
            <v-select :items="items" label="הגדרת אזור"></v-select>
          </v-col>
        </v-row>

        <v-row justify="center">
          <v-col class="mb-4">
            <div>
              <v-text-field v-model="structure_size" label="גודל המבנה" required></v-text-field>
            </div>
          </v-col>
        </v-row>

        <v-row justify="center">
          <v-col class="mb-4">
            <div>
              <v-text-field v-model="num_primary_enternces" label="מספר כניסות ראשיות" required></v-text-field>
            </div>
          </v-col>
        </v-row>

        <v-row justify="center">
          <v-col class="mb-4">
            <div>
              
              <v-text-field v-model="num_secondary_enternces" label="מספר כניסות משניות" required></v-text-field>
            </div>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import CONSTANTS from "../CONSTANTS";
export default {
  name: "SegerCalc",

  data: () => ({
    calc_params: {
      structure_size: 500,
      num_primary_enternces: 5,
      num_secondary_enternces: 0,
      color: CONSTANTS.COLORS.green
    },
    items: ["ירוק", "צהוב", "כתום", "אדום"],
    structure_size: 500,
    num_primary_enternces: 5,
    num_secondary_enternces: 0,
    structure_open: true,
    color: CONSTANTS.COLORS.green,
    num_groups: 1,
    result: null
  }),

  mounted: function() {
    this.getResult();
  },
  watch: {
    color: function() {
      this.calc_params.color = this.color.toString();
      this.getResult();
    },
    structure_size: function() {
      this.calc_params.structure_size = +this.structure_size;
      this.getResult();
    },
    num_primary_enternces: function() {
      this.calc_params.num_primary_enternces = +this.num_primary_enternces;
      this.getResult();
    },
    num_secondary_enternces: function() {
      this.calc_params.num_secondary_enternces = +this.num_secondary_enternces;
      this.getResult();
    }
  },
  computed: {
    HEBREW_COLORS: function() {
      return CONSTANTS.HEBREW_COLORS;
    }
  },
  methods: {
    getResult: function() {
      const max_people = this.calc_params.structure_size / 4; //Max people - one for every 4 sqM
      const factor =
        this.calc_params.num_primary_enternces * 3 +
        this.calc_params.num_secondary_enternces * 2;

      const temp_result = Math.floor(
        Math.round(
          Math.min(
            max_people,
            factor *
              CONSTANTS.GROUP_SIZE_IN_CLOSED_STRUCTURE[this.calc_params.color]
          )
        )
      );

      this.result =
        temp_result -
        (temp_result %
          CONSTANTS.GROUP_SIZE_IN_CLOSED_STRUCTURE[this.calc_params.color]);

      this.num_groups = Math.floor(
        Math.max(
          1,
          this.result /
            CONSTANTS.GROUP_SIZE_IN_CLOSED_STRUCTURE[this.calc_params.color]
        )
      );
    }
  }
};
</script>
